'use strict';

function showUserMessage(text, delay) {
  console.log('showUserMessage', text);
  var div = document.createElement('div');
  div.id = 'driveslides-usermessage';
  div.innerHTML = text;
  div.style.position = 'fixed';
  div.style.top = '40px';
  div.style.left = '50%';
  div.style.zIndex = 1000002;
  div.style.width = '500px';
  div.style.background = 'rgba(50, 50, 50, 0.96)';
  div.style.boxShadow = '0 1px 3px rgba(0,0,0,0.6)';
  div.style.borderRadius = '3px';
  div.style.color = '#ffffff ';
  div.style.marginLeft = '-250px';
  div.style.padding = '16px 24px 14px';
  div.style.fontSize = '14px';
  div.style.lineHeight = '20px';
  div.style.minHeight = '20px';
  div.style.textAlign = 'center';
  div.style.fontFamily = 'Roboto, sans-serif';

  var a = div.children[0];
  // If link inside
  if (a) {
    a.style.textDecoration = 'underline';
    a.style.color = '#a1c2fa ';
    a.style.color = '#a1c2fa ';
    a.style.fontSize = '15px';
    a.style.fontWeight = 'bold';
    a.style.marginTop = '15px';
    a.style.display = 'block';
    a.style.outline = 0;

    a.onmouseenter = function () {
      this.style.textDecoration = 'none';
    };
    a.onmouseout = function () {
      this.style.textDecoration = 'underline';
    };
  }

  document.body.appendChild(div);
  if (delay) {
    setTimeout(hideUserMessage, delay * 1000);
  }
}

function hideUserMessage() {
  var div = document.getElementById('driveslides-usermessage');
  if (div) {
    div.style.display = 'none';
    div.remove();
  }
}